import { fetchDoard, deleteDoard } from '@/api/boardList'; //db api
const SORT_LS = 'sortNumber'; //   
export default {
	namespaced: true, // 
	state: () => ({
		sortCount: localStorage.getItem(SORT_LS) || '10', //  
		sortCountOptions: [
			//  
			{ value: '1', text: '1' },
			{ value: '5', text: '5' },
			{ value: '10', text: '10' },
			{ value: '20', text: '20' },
			{ value: '30', text: '30' },
			{ value: '50', text: '50' },
		],
		loading: false, //
		listData: [],
		listPagination: {},
		totalCnt: '',
		pageNum: 1,
		searchTrue: false, // ,
		searchText: null,
		deleteId: [],
		isCategory: '',
		startDay: '',
		endDay: new Date(),
	}),
	mutations: {
		updateState(state, payload) {
			Object.keys(payload).forEach(key => {
				state[key] = payload[key]; //state  
			});
		},
	},
	actions: {
		deleteAllCheck({ commit }, payload) {
			commit('updateState', {
				deleteId: payload,
			});
		},
		async deleteData({ state, commit, dispatch }, payload) {
			await deleteDoard(
				payload.pageName, //db api 
				state.deleteId,
			);
			dispatch('getList', payload);
			commit('updateState', {
				deleteId: [],
			});
		},
		async getList({ state, commit }, payload) {
			commit('updateState', {
				deleteId: [],
			});
			commit('updateState', {
				loading: true, //   
			});
			const searchName = payload.searchText
				? `&searchKeyword=${payload.searchText}`
				: ''; //    
			const searchYn = payload.searchCategory
				? `&searchCategory=${payload.searchCategory}`
				: `&searchCategory=${state.isCategory}`; //  
			const seachStartDate = payload.seachStartDate
				? `&searchStartDate=${payload.seachStartDate}`
				: commit('updateState', {
						startDay: '',
				  });
			const seachEndDate = payload.seachEndDate
				? `&searchEndDate=${payload.seachEndDate}`
				: '';

			const { data } = await fetchDoard(
				payload.pageName, //db api 
				payload.pageNum, // 
				payload.sortNumber, //  
				searchName,
				searchYn,
				seachStartDate, //
				seachEndDate, //
			);
			localStorage.setItem(SORT_LS, payload.sortNumber);
			commit('updateState', {
				listData: data.result.data,
				listPagination: data.result.pagination,
				sortCount: payload.sortNumber,
				pageNum: data.result.pagination.page,
			});
			setTimeout(() => {
				commit('updateState', {
					loading: false, //   
				});
			}, 100);
		},
	},
};
